<template>
 <div class="listview"  ref="wrapper">
   <ul>
     <li v-for="group in data" class="list-group">
       <h2 class="list-group-title">{{group.title}}</h2>
       <ul>
       <li   @click="selectItem(item)" v-for="item in group.items" class="list-group-item" :src="group.items">
       <img class="avatar" :src="item.avatar">
       <span class="name">{{item.name}}</span>
       </li>
       </ul>
     </li>
   </ul>
 </div>
</template>

<script type="text/ecmascript-6">
  import Bscroll from "better-scroll"
  export default {
    props: {
      data: {
        type: Array,
        default: function () {
          return []
        }
      }
    },
    mounted() {
      setTimeout(() => {
        this.scroll = new Bscroll(this.$refs.wrapper,{click:true})
      }, 20)
    },

    watch: {
      letter() {
        if (this.letter) {
          const element = this.$refs[this.letter][0]
          console.log(element)
          this.scroll.scrollToElement(element)
        }
      }
    },
    methods: {
      selectItem(item){
        this.$emit("select",item)
      },
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"

  .listview
    position: relative
    width: 100%
    height: 100%
    overflow: hidden
    background: $color-background
    .list-group
      padding-bottom: 30px
      .list-group-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
      .list-group-item
        display: flex
        align-items: center
        padding: 20px 0 0 30px
        .avatar
          width: 50px
          height: 50px
          border-radius: 50%
        .name
          margin-left: 20px
          color: $color-text-l
          font-size: $font-size-medium
    .list-shortcut
      position: absolute
      z-index: 30
      right: 0
      top: 50%
      transform: translateY(-50%)
      width: 20px
      padding: 20px 0
      border-radius: 10px
      text-align: center
      background: $color-background-d
      font-family: Helvetica
      .item
        padding: 3px
        line-height: 1
        color: $color-text-l
        font-size: $font-size-small
        &.current
          color: $color-theme
    .list-fixed
      position: absolute
      top: 0
      left: 0
      width: 100%
      .fixed-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
    .loading-container
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



